.team-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
	z-index: 999;
}

.team-logo {
	width: 80px;
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 3vh;
	z-index: 999;
	margin-left: 2%;
}

.team-main-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	width: 100%;
	padding-top: 20px;
}

.team-wrapper {
	padding-left: 50px;
	display: flex;
	flex-direction: column;
}

.team-title {
	width: 100% !important;
}

.team-img {
	width: 100%;
    padding-top: 20px;
	margin-bottom: 20px;
	
}

.team-img img {
	width: 100%;
	z-index: 999;
}

.team-head {
    width: 100%;
    color: var(--secondary-color);
	font-family: var(--secondary-font);
	font-size: 20px;
	font-weight: 700;
    padding-top: 40px;
}